  text-align: center;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
  overflow: hidden; /* 確保內容不超出按鈕邊界 */
  display: flex;
  justify-content: center; /* 水平居中 */
  align-items: center; /* 垂直居中 */
  width: 140px; /* 固定寬度 */
  height: 80px; /* 固定高度 */
