  padding: 10px; /* 增加內部間距，防止文字緊貼邊框 */
  word-break: break-word; /* 允許長文字換行 */
  white-space: normal; /* 避免文字不換行 */
}

.workflow-button.completed {
  background-color: #ffffcc; /* 保持按鈕背景為黃色 */
  color: #000; /* 文字顏色為黑色 */
}

.workflow-description {
  margin-top: 10px;
