  font-size: 14px;
  color: #555;
}

.history-section {
  margin-top: 30px;
}

.history-section h2 {
  font-size: 18px;
  font-weight: bold;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

th, td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

th {
  background-color: #f4f4f4;
}

.modal-overlay {
  position: fixed; /* 確保彈窗覆蓋整個視窗 */
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5); /* 黑色半透明背景 */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000; /* 高於其他元素 */
}

.modal-content {
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* 添加陰影 */
  width: 40%;
  z-index: 1100; /* 必須高於 modal-overlay 的內容 */
}

.form-group {
  margin-bottom: 15px;
}

label {
  display: block;
  font-weight: bold;
  margin-bottom: 5px;
}

input,
textarea,
select {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

textarea {
  resize: none;
}

.form-actions {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.submit-btn {
  background-color: #4caf50;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
}

.cancel-btn {
  background-color: #f44336;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
}

.circle {
  position: absolute; /* 絕對定位 */
  top: 50%; /* 垂直居中 */
  left: 50%; /* 水平居中 */
  transform: translate(-50%, -50%); /* 將圓心調整到按鈕中心 */
  width: 30px; /* 圓圈大小 */
  height: 30px; /* 圓圈大小 */
  border: 5px solid #f5222d; /* 圓圈的邊框顏色和粗細 */
  border-radius: 50%; /* 確保圓形 */
  background-color: transparent; /* 圓圈內部透明 */
  z-index: 1; /* 保證圓圈覆蓋在文字上 */
}

/*直*/
@media screen and (max-width: 480px) {

  .header{
    width: 100%;
  }
  .main-content {
    flex-direction: column; /* 小螢幕時內容垂直排列 */
  }

  .image-container {
    width: 100%; /* 手機上圖片容器占滿整個屏幕 */
    height: 100%;
  }

  .order-image {
    width: 100%; /* 圖片寬度占滿容器 */
    max-height: 400px; /* 限制圖片高度以適應屏幕 */
  }

  .workflow-button {
    grid-template-columns: repeat(5, 1fr); /* 每排 6 個按鈕 */
    color: black !important; /* 確保文字保持黑色 */
  }

  th,
  td {
    color: black !important; /* 確保文字保持黑色 */
  }

  .modal-content {
    width: 80%; /* 在手機上寬度設置為屏幕的 90% */
    max-width: 80%; /* 保持屏幕邊距 */
    max-height: 80vh; /* 避免超出屏幕高度 */
    padding: 15px; /* 減少填充 */
  }
}

/*平板橫*/
@media screen and (min-width: 768px) and (max-width: 1024px) {
  .main-content {
    flex-direction: row; /* 橫屏時回到橫向排列 */
