    overflow-y: auto; /* 垂直滾動 */
    z-index: 1100; /* 必須高於 modal-overlay 的內容 */
  }
  
}

/* 手機橫式 */
@media screen and (min-width: 481px) and (max-width: 767px) {

  .main-content {
    flex-direction: row; /* 橫屏時回到橫向排列 */
