  }

    .image-container {
    width: auto;
    min-width: 300px; /* 設置最小寬度 */
  }

  .order-image {
    width: 100%; /* 讓圖片適應容器 */
    height: auto; /* 保持比例 */
  }

    .workflow-button {
    grid-template-columns: repeat(5, 1fr); /* 每排 6 個按鈕 */
    color: black !important; /* 固定文字顏色為黑色 */
    background-color: #ffffcc; /* 保持背景顏色一致 */
  }

    th, td {
      color: black !important; /* 表格文字顏色固定為黑色 */
